:root{
    --color-primario: rgb(212, 212, 212);
  }

.button{
    height: 80px;
    flex: 1 1;
    margin: 10px;
    border: 8px solid var(--color-primario);
    border-radius: 20px;
    background-color: black;
    color: var(--color-primario);
    font-size: 2em;
    cursor: pointer;
    transition: all 0.2s ease;
}

.button:hover{
    border: 8px solid #ffff;
    transition: all 0.2s ease;
    color: #ffff
}

.input{
    text-align: right;
    align-items: center;
}